export class ScrollPosition {
    public left: number;
    public top: number;
    public x: number;
    public y: number;
    
    constructor(left: number = 0, top: number = 0, x: number = 0, y: number = 0) {
        this.left = left;
        this.top = top;
        this.x = x;
        this.y = y;
    }
}
